@import url(https://fonts.googleapis.com/css2?family=Frijole&display=swap);
@font-face{font-family:"Anor";src:url(/static/media/Archi10.083787dc.TTF) format("truetype")}@font-face{font-family:'Abw';src:url(/static/media/Archi18.329828b4.TTF) format("truetype")}:root{--grey: #676767;font-size:calc( 6px + 1vw)}*{box-sizing:border-box}html,body{margin:0;height:100%;width:100%}a{text-decoration:none;color:inherit}p{margin:0}

.landing-page__title h1{font-weight:200;font-size:4em;margin:.5em 0 0;letter-spacing:.2em;cursor:pointer}.landing-page__title h2{font-size:1.1rem;margin:0 0 2em;letter-spacing:.18em}

.App{font-family:Georgia, 'Times New Roman', Times, serif;color:var(--grey);text-align:center;display:grid;grid-template-columns:1fr;min-height:calc(var(--vh) * 100)}.App #out{font-size:3.5em;font-weight:900;margin:0;align-self:end}.App #btns{display:flex;align-self:flex-end;flex-direction:column;justify-content:space-between}.App #enter-btn,.App #order-here{display:inline-block;align-self:end;font-size:3.5rem;letter-spacing:.1em;font-weight:900;height:1em;width:9em;position:relative;padding:1rem 0 0;margin:0 auto;font-family:'Times New Roman', Times, serif;background-color:#00000000;border:none;color:inherit;outline:none}.App #order-here{display:block;position:relative;margin:1em auto 2em;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:10em;font-size:2.5rem;border:1px solid var(--grey);opacity:.8;padding:10px}.App #no-route-menu{display:inline-block;align-self:end}.App #no-route-menu{position:absolute;justify-content:center;align-items:center;top:70%;position:absolute;left:50%;transform:translate(-50%, -50%)}.App #no-route-menu p{margin-bottom:20px}.App #no-route-menu a{padding:0;margin:0 5px}.about-page__container{font-family:Georgia, 'Times New Roman', Times, serif;color:var(--grey);text-align:center;display:grid;grid-template-columns:1fr;grid-template-rows:repeat(4, 1fr);min-height:100vh;min-width:100vw}@media (min-width: 900px){.App h3{font-size:2.5rem}.App #enter-btn{font-size:2rem}}

.footer{width:100%;position:relative;background-color:#67678010;padding:1em 0;color:var(--grey);font-size:1rem;align-self:flex-end}.footer p{padding:0.2em 0}.footer .footer-logo__section{display:flex;justify-content:center;align-items:center}.footer .footer-logo__section a img{width:3em;margin:0 1em}.footer .footer-contact-images{display:flex;justify-content:center;align-items:center;padding-top:.5em}.footer .footer-contact-images a img{width:1.5em;margin:0 .7em}.footer .footer-credits #wd{font-family:'Frijole', cursive;font-size:.9rem}@media (min-width: 900px){.footer{font-size:.7rem}.footer .footer-credits #wd{font-size:.5rem}}

.background{position:fixed;z-index:-100;height:100%;width:100%}.background img{height:100%;width:100%;object-fit:cover}

.about-container{display:flex;flex-direction:column;position:relative;margin:0 auto;padding:0 3.5em}.about-container .about-container__snippets{padding:2em 0}.about-container .about-container__snippets p{letter-spacing:.2em;font-size:1.4rem;padding:1em 0}.about-container .about-container__snippets iframe{opacity:.7;z-index:10}@media (min-width: 900px){.about-container{flex-direction:row;flex-wrap:wrap;max-width:1200px}.about-container .about-telurian{order:2;width:60%}.about-container .about-telurian p{text-indent:0;text-align:left;font-size:1.2rem;line-height:1.7}.about-container .about-telurian h3{text-align:left;font-size:2rem}.about-container .about-container__snippets{order:3;width:100%}.about-container .opuses-container{order:1;width:40%;text-align:right}.about-container .opuses-container .opus{font-size:1.2rem}.about-container .opuses-container .opus p{font-size:2rem}}

.opus{font-size:1.6rem;list-style:none;margin:0;padding:0}.opus p{font-feature-settings:"smcp";font-variant:small-caps;font-size:2.2rem;letter-spacing:.1em;padding:1em 0;font-weight:900}.opus li{padding:.2em 0}

.about-telurian{padding:0 0 0 1em}.about-telurian h3{font-size:2.2rem;text-align:center;font-feature-settings:"smcp";font-variant:small-caps}.about-telurian p{text-align:justify;font-size:1.7rem;text-indent:1em}.about-telurian a{border:1px solid #00000020;border-radius:2px;padding:2px 20px;background-color:#00000040;color:#ffc;font-size:.7rem}

.items-container{position:relative;max-width:1200px;margin:0 auto;padding:0 1.7em}.items-container p:first-child,.items-container #order-page__punch-line{font-feature-settings:"smcp";font-variant:small-caps;font-weight:900;font-size:1.4rem;letter-spacing:.2em;padding:2em 0}.items-container #order-page__punch-line{padding:0 0 1em}.items-container .order-page__shipping{text-align:left;padding:0 2em;font-size:1.2rem}.items-container .order-page__shipping p:first-child{font-size:2rem;padding:0 0 .5em}.items-container .order-page__shipping p:nth-child(2),.items-container .order-page__shipping p:nth-child(5),.items-container .order-page__shipping p:nth-child(8),.items-container .order-page__shipping p:nth-child(11),.items-container .order-page__shipping p:nth-child(12){font-weight:900;margin:1em 0}

.item{display:flex;flex-wrap:wrap;padding:3em 2em;color:var(--grey)}.item .item-photo{position:relative;align-self:start;display:flex;flex-direction:column;width:100%}.item .item-photo .sold-out{position:absolute;top:0;left:0;height:100%;width:100%;z-index:100;background:#00000090}.item .item-photo .sold-out:before{content:'SOLD OUT';display:inline-block;font-size:40px;font-family:'Abw';color:#fff;transform:rotate(315deg);position:absolute;top:37%;left:-27%;width:150%;padding:10px 0}@media (min-width: 768px){.item .item-photo .sold-out:before{font-size:45px}}.item .item-photo img{position:relative;width:100%;margin:0 auto}.item .item-photo button{margin:.5em 0;font-size:1rem;width:auto;color:var(--grey);border:none;background-color:#00000000;cursor:pointer;text-transform:uppercase;font-weight:900;text-shadow:1.5px 1.5px 1px white}.item .item-photo .paypal{position:relative;margin:0 auto;width:100%}.item .item-photo .paypal select{width:60%;height:30px;font-size:1.1rem;margin-top:.7em}.item .item-photo .paypal input{width:100%}.item .item-photo .paypal .buy-now{border:1px solid var(--grey);border-radius:5px;margin-top:.4em;padding:0 1em;width:60%;opacity:.8}.item .item-description{align-self:start;padding-left:2em;width:100%}.item .item-description h3{margin:10px 0 0 0;padding-bottom:1em;font-size:1.4rem;text-align:center}.item .item-description p{text-align:left;font-size:1.4rem;font-weight:400}.item .item-description p:last-child{margin-top:10px;font-weight:700;font-size:.8rem}@media (min-width: 540px){.item .item-photo{width:35%}.item .item-photo .sold-out:before{font-size:30px}.item .item-photo .paypal select{width:100%}.item .item-photo .paypal .buy-now{width:100%}.item .item-description{width:65%}}@media (min-width: 900px){.item .item-photo{width:27%}.item .item-photo .paypal{width:100%}.item .item-photo .paypal select{width:100%;font-size:.8rem;color:var(--grey)}.item .item-photo .paypal .buy-now{margin-top:1em;width:60%}.item .item-description{width:73%}.item .item-description h3{font-size:1.2rem}.item .item-description p{font-size:1.1rem}}

.hamburger-container{position:fixed;top:0;right:-13px;font-size:2rem;z-index:100}.hamburger-container #hamburger-menu-logo{height:3rem}.hamburger-container .hamburger-links ul{text-align:center;padding:0}.hamburger-container .hamburger-links ul li{list-style:none;padding:.01em 1em;margin-bottom:.4em}

.tshirts-container{max-width:1000px;margin:0 auto}.tshirts-container p:first-child,.tshirts-container p:nth-child(2){font-size:1.3rem}.tshirts-container p:first-child{letter-spacing:.3em;margin-bottom:.5rem}.tshirts-container p:nth-child(2){letter-spacing:.17rem;margin-bottom:2rem}.tshirts-container p:nth-child(3),.tshirts-container p:nth-child(4),.tshirts-container p:nth-child(5){font-size:1.1rem;margin-bottom:.3rem}.tshirts-container p:nth-child(5){letter-spacing:.2em}.tshirts-container p:nth-child(6){margin-top:1rem}.tshirts-container .tshirts-render{position:relative;padding-bottom:4em;margin:0 auto}.tshirts-container .tshirts-render .tshirt{padding:3rem 0 3rem}.tshirts-container .tshirts-render .tshirt p{font-size:1.4rem;margin-bottom:1rem;font-weight:900}.tshirts-container .tshirts-render .tshirt .tshirts-images{display:flex;flex-wrap:wrap;justify-content:center}.tshirts-container .tshirts-render .tshirt .tshirts-images img{width:70%;height:100%;object-fit:cover;display:block;margin-bottom:1em}.tshirts-container .tshirts-render .tshirt .tshirt-paypal form{display:flex;flex-direction:column}.tshirts-container .tshirts-render .tshirt .tshirt-paypal form select{position:relative;width:50%;margin:0 auto}.tshirts-container .tshirts-render .tshirt .tshirt-paypal form .buy-now{position:relative;margin:1rem auto 0;border:1px solid var(--grey);border-radius:5px;margin-top:.4em;padding:0 1em;width:50%;opacity:.8}@media (min-width: 760px){.tshirts-container .tshirts-render{max-width:600px}.tshirts-container .tshirts-render .tshirt p{font-size:1.2rem}.tshirts-container .tshirts-render .tshirt .tshirts-images{justify-content:space-evenly}.tshirts-container .tshirts-render .tshirt .tshirts-images img{width:45%;display:block}.tshirts-container .tshirts-render .tshirt .tshirt-paypal form select{width:50%;font-size:.65rem}.tshirts-container .tshirts-render .tshirt .tshirt-paypal form .buy-now{width:30%}}

#about-telurian,#about-tellurium{width:90%;margin:0 auto;max-width:800px;text-indent:20px;text-align:justify}#about-telurian h3,#about-tellurium h3{font-size:2rem;text-align:center;font-feature-settings:"smcp";font-variant:small-caps}#about-telurian p,#about-tellurium p{font-size:1.5rem;letter-spacing:.02em;line-height:1.4}#about-telurian img,#about-tellurium img{display:block;position:relative;width:30%;padding:2em 0 4em;margin:0 auto}#about-telurian a,#about-tellurium a{display:block;text-align:center;font-weight:900;font-size:1.5rem;padding:1em 0}#about-tellurium .about-tellurium-photos{display:flex;justify-content:center}#about-tellurium .about-tellurium-photos img{width:30%;height:100%;transition:transform .5s ease}#about-tellurium .about-tellurium-photos img:hover{transform:scale(1.8)}@media (min-width: 900px){#about-telurian p,#about-tellurium p{font-size:1.2rem}}

.images-gallery{position:relative;width:100%;flex-flow:row wrap;display:flex;justify-content:center;align-items:center}.images-gallery img{width:90%}.images-gallery #left,.images-gallery #right,.images-gallery #close{font-size:30px;z-index:10;color:#bbb;cursor:pointer}.images-gallery #left{margin-right:-40px}.images-gallery #right{margin-left:-40px}.images-gallery #close{margin-top:-50px}.images-gallery p{width:100%;margin:-20px 0 0;padding:0}@media (min-width: 1025px){.images-gallery{width:60%;max-width:900px;margin:0 auto}.images-gallery #close{margin:0}.images-gallery p{margin:0}}

